<template>
    <div class="mychart">
        <div class="data-area">
            <ul>
                <li class="data-item" v-for="item in uiModel">
                    <input type="number" class="data-input" v-model="item.win" />
                    <input type="number" class="data-input" v-model="item.draw" />
                    <input type="number" class="data-input" v-model="item.lose" />
                    <input type="number" class="data-input" v-model="item.time" v-on:focus="addToModel()" />
                </li>
            </ul>
        </div>
        <div class="chart-ctn" id="chartsCtn"></div>
        <div class="btn-ctn">
            <button class="normal-btn" v-on:click="initCharts">绘图</button>
            <button class="normal-btn" v-on:click="reset">重置</button>
            <span>{{avageValue}}</span>
        </div>
    </div>
</template>
<script>
var eCharts = require('echarts');
export default {
    name: 'myCharts',
    data() {
        return {
            uiModel: [{
                win: '',
                draw: '',
                lose: '',
                time: ''
            }]
        };
    },
    methods: {
        addToModel: function (ite) {
            if (this.uiModel.length < 5) {
                this.uiModel.push({
                    win: '',
                    draw: '',
                    lose: '',
                    time: ''
                });
            }
        },
        reset: function () {
            this.uiModel = [{
                win: '',
                draw: '',
                lose: '',
                time: ''
            }];
        },
        initCharts: function () {
            // 数组二次操作，用于渲染
            if (this.uiModel[this.uiModel.length - 1].win === '') {
                this.uiModel.pop();
            }
            this.uiModel = this.uiModel.reverse();
            // 图表渲染
            var myCharts = eCharts.init(document.getElementById('chartsCtn'));
            myCharts.setOption({
                tooltip: {
                    trigger: 'axis'
                },
                grid: {
                    left: '3%',
                    right: '4%',
                    bottom: '3%',
                    containLabel: true
                },
                legend: {
                    data: [{ name: 'win' }, { name: 'draw' }, { name: 'lose' }]
                },
                toolbox: {
                    feature: {
                        saveAsImage: {}
                    }
                },
                xAxis: {
                    type: 'value',
                    splitLine: {
                        lineStyle: {
                            type: 'dashed'
                        }
                    },
                    max: 'dataMax',
                    min: 'dataMin'
                },
                yAxis: {
                    type: 'value',
                    splitLine: {
                        lineStyle: {
                            type: 'dashed'
                        }
                    },
                    max: 'dataMax',
                    min: 'dataMin'
                },
                series: [
                    {
                        name: 'win',
                        type: 'line',
                        data: this.uiModel.map((v) => [v.time, v.win])
                    },
                    {
                        name: 'draw',
                        type: 'line',
                        data: this.uiModel.map((v) => [v.time, v.draw])
                    }, {
                        name: 'lose',
                        type: 'line',
                        data: this.uiModel.map((v) => [v.time, v.lose])
                    }
                ]
            });
        }
    },
    computed: {
        avageValue: function () {
            let sum = 0;
            for (let i = 0; i < this.uiModel.length; i++) {
                if (this.uiModel[i].win) {
                    sum += parseFloat(this.uiModel[i].win);
                }
            }
            if (this.uiModel.length > 1) {
                if (this.uiModel[this.uiModel.length - 1].win) {
                    return (sum / this.uiModel.length).toFixed(2);
                } else {
                    return (sum / (this.uiModel.length - 1)).toFixed(2);
                }
            } else {
                return 'Message!';
            }
        }
    }
};
</script>
<style>
.mychart {
    margin-bottom: 60px;
}

.chart-ctn {
    width: 96%;
    height: 400px;
    margin: 10px auto;
    border: 8px groove cornflowerblue;
    border-radius: 5px;
}

.data-area {
    max-height: 160px;
    margin: 20px 10px;
    border-bottom: 2px solid #888888;
    overflow-y: auto;
}

.data-item {
    margin-bottom: 10px;
}

.data-input {
    width: 60px;
    border-radius: 60px;
}

.btn-ctn {
    position: fixed;
    width: 100%;
    bottom: 0px;
    margin: 0 auto;
}
</style>



