<template>
  <div class="mytest">
    <h1>{{message}}</h1>
    <div class="record_list">
      <div class="record_list_model" v-for="item in model.host">
        <input class="record_input" type="number" placeholder="" v-model="item.total">
      </div>
    </div>
    <div class="record_list">
      <div class="record_list_model" v-for="item in model.guest">
        <input class="record_input" type="number" placeholder="" v-model="item.total">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'mytest',
  data() {
    return {
      message: '张氏指数测试区',
      model: {
        host: {
          total: '',
          host: '',
          latest: ''
        },
        guest: {
          total: '',
          host: '',
          latest: ''
        }
      }
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



