<template>
    <div>
        <div class="record_list">
            <div class="record_list_model">
                <input class="record_input" type="number" placeholder="The Home Team Goal 5" v-model="model.hs">
            </div>
            <div class="record_list_model">
                <input class="record_input" type="number" placeholder="The Visiting Team Goal 5" v-model="model.gs">
            </div>
            <div class="record_list_model">
                <input class="record_input" type="number" placeholder="Home Goaled 5" v-model="model.hn">
            </div>
            <div class="record_list_model">
                <input class="record_input" type="number" placeholder="Visiting Goaled 5" v-model="model.gn">
            </div>
            <div class="record_list_model">
                <input class="record_input" type="number" placeholder="Home Goal 10" v-model="model.hs2">
            </div>
            <div class="record_list_model">
                <input class="record_input" type="number" placeholder="Visiting Goal 10" v-model="model.gs2">
            </div>
            <div class="record_list_model">
                <input class="record_input" type="number" placeholder="Home Goaled 10" v-model="model.hn2">
            </div>
            <div class="record_list_model">
                <input class="record_input" type="number" placeholder="Visiting Goaled 10" v-model="model.gn2">
            </div>
            <div class="record_list_model">
                <input class="record_input" type="number" placeholder="leftWinRate" v-model="model.hRate">
            </div>
            <div class="record_list_model">
                <input class="record_input" type="number" placeholder="rightWinRate" v-model="model.gRate" v-on:keyup.enter="calculate">
            </div>
        </div>
        <div class="add_record_btn">
            <div class="record_list_item">
                <input class="record_input3" type="number" placeholder="" disabled="disabled" v-model="left1">
                <input class="record_input3" type="number" placeholder="" disabled="disabled" v-model="right1">
                <input class="record_input3" type="number" placeholder="" disabled="disabled" v-model="left2">
                <input class="record_input3" type="number" placeholder="" disabled="disabled" v-model="right2">
            </div>
            <button class="record_btn_style" v-on:click='calculate'>Cal</button>
            <button class="record_btn_style">
                <router-link class="noDe" to="/">Home</router-link>
            </button>
            <button class="record_btn_style">
                <router-link class="noDe" to="/myrecord">Record</router-link>
            </button>
            <button class="record_btn_style" v-on:click='resetStore'>{{message}}</button>
        </div>
    </div>
</template>
<script>
import Store from '../store';
export default {
    name: 'mycal',
    data() {
        return {
            model: {
                hs: '',
                hn: '',
                gs: '',
                gn: '',
                hs2: '',
                hn2: '',
                gs2: '',
                gn2: '',
                hRate: '',
                gRate: ''
            },
            left1: '',
            left2: '',
            right1: '',
            right2: '',
            message: 'Winner!',
            modelList: Store.fetch().length > 0 ? Store.fetch() : [],
            teamName: ''
        };
    },
    methods: {
        calculate: function () {
            for (let item in this.model) {
                this.model[item] = parseFloat(this.model[item]);
            }
            this.left1 = this.sortRate(this.model.hs, this.model.gn) * this.model.hRate;
            this.left2 = this.sortRate(this.model.hs2, this.model.gn2);
            this.right1 = this.sortRate(this.model.gs, this.model.hn) * this.model.gRate;
            this.right2 = this.sortRate(this.model.gs2, this.model.hn2);
            this.message = (((this.left1 + this.left2) - (this.right1 + this.right2)) / 2).toFixed(2);
            this.teamName = window.prompt('请输入队名！');
            if (this.teamName) {
                this.addToList();
            }
            this.model = {
                hs: '',
                hn: '',
                gs: '',
                gn: '',
                hs2: '',
                hn2: '',
                gs2: '',
                gn2: '',
                hRate: '',
                gRate: ''
            };
        },
        sortRate: function (num1, num2) {
            if (num1 > num2) {
                return (num2 + (num1 - num2) * this.sortPercent(num1 - num2) / 2);
            } else {
                return (num1 + (num2 - num1) * this.sortPercent(num2 - num1) / 2);
            }
        },
        sortPercent: function (num) {
            num = Math.abs(num);
            if (num >= 0 && num <= 0.3) {
                return 1;
            } else if (num > 0.3 && num <= 0.6) {
                return 0.85;
            } else if (num > 0.6 && num < 1) {
                return 0.75;
            } else {
                return 0.5;
            }
        },
        addToList: function () {
            debugger;
            this.modelList.push({
                l1: this.left1,
                l2: this.left2,
                r1: this.right1,
                r2: this.ritht2,
                teamName: this.teamName,
                message: this.message
            });
            Store.save(this.modelList);
        },
        resetStore: function () {
            if (window.confirm('确定清空储存？')) {
                Store.reset();
            };
        }
    }
};
</script>
<style>
.record_input {
    width: 100%;
    height: 30px;
    font-size: 1rem;
    text-align: center;
    line-height: 20px;
    border: 5px groove;
}

.record_input3 {
    width: 21%;
    height: 30px;
    font-size: 1rem;
    text-align: center;
    line-height: 20px;
    border: 5px groove cornflowerblue;
}

.record_list_model:nth-child(2n+1) input {
    border: 5px groove #289ed4;
}

.add_record_btn {
    border-top: 2px solid;
    padding-top: 5px;
}

.record_btn_style {
    width: 49%;
    float: left;
    text-decoration: none;
    background: indianred;
    margin-top: 2px;
    padding: 0;
    height: 40px;
    line-height: 40px;
    font-size: 1.8rem;
}

.noDe {
    text-decoration: none;
    color: black;
}
</style>



