<template>
  <div class="myrecord">
    <h2>{{msg}}</h2>
    <ul>
      <li v-for="item in myList" class="">
        <span class="list-key">状态比：</span>{{item.message}}
        <span class="list-key">主队名：</span>{{item.teamName}}
        <input type="number" v-model="item.profit" placeholder="profit" />
      </li>
    </ul>
    <textarea class="log" v-show="showLog" v-model="editModel">
    </textarea>
    <button class="center-btn" v-on:click="toggleLog">{{showLog ? 'HideLog':'ShowLog'}}</button>
    <button class="center-btn" v-on:click="save">Save</button>
    <button class="center-btn" v-on:click="showProfits">Profits</button>
    <button class="center-btn" v-show="showLog" v-on:click="saveLog">SaveLog</button>
  </div>
</template>

<script>
import Store from '../store.js';
export default {
  name: 'myrecord',
  data() {
    return {
      msg: 'My Record',
      showLog: false,
      myList: (Store.fetch().length > 0 ? Store.fetch() : []),
      editModel: []
    };
  },
  components: {
    Store
  },
  methods: {
    toggleLog: function () {
      this.showLog = !this.showLog;
      this.editModel = JSON.stringify(this.myList);
    },
    save: function () {
      Store.save(this.myList);
    },
    showProfits: function () {
      let sum = 0;
      this.myList.forEach((v) => {
        if (v.profit) {
          sum += parseFloat(v.profit);
        }
      });
      this.msg = sum.toFixed(1);
    },
    saveLog: function () {
      debugger;
      Store.save(JSON.parse(this.editModel));
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  max-height: 500px;
  padding: 0;
  overflow-y: auto;
}

li {
  position: relative;
  border-left: 10px solid red;
  border-top: 1px solid;
  border-bottom: 1px solid;
  height: 40px;
  line-height: 40px;
}

li:nth-child(2n+1) {
  border-left: 10px solid #7979c7;
}

a {
  color: #42b983;
}

.log {
  width: 80%;
  text-align: center;
  background-color: #C7EDCC;
  margin: 10px auto;
  padding: 10px 20px;
  border-radius: 4px;
  border-radius: 4px;
  box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.5);
}

.center-btn {
  background: firebrick;
  width: 200px;
  height: 48px;
  border-radius: 4px;
  font-size: 1.6rem;
  margin: 10px;
}

.list-key {
  font-weight: bold;
}

li input {
  position: absolute;
  top: 0px;
  right: 0px;
  height: 30px;
  width: 40px;
  text-align: center;
  font-size: 1.2rem;
  border-radius: 50%;
  margin: 2px 1px;
}
</style>



