<template>
  <div class="hello">
  
    <div v-for="item in routes" class="nav-item" v-on:click="goto(item)">{{item}}</div>
  </div>
</template>

<script>
import router from '../router/index';
export default {
  name: 'hello',
  data() {
    return {
      routes: ['MyCharts', 'MyRecord', 'Log', 'Mycal']
    };
  },
  methods: {
    goto: function (routeName) {
      router.push(routeName);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.nav-item {
  display: inline-block;
  width: 200px;
  height: 200px;
  margin: 16px 10px;
  text-align: center;
  line-height: 60px;
  color: #fff;
  font-size: 2rem;
  border: 2px solid #000;
  border-radius: 10px;
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.5);
  background-image: url(../assets/navitem.jpg);
  background-size: 100% 100%;
  background-repeat: no-repeat;
  transition: 1s;
  -moz-transition: 1s;
  -webkit-transition: 1s;
}

.nav-item:hover {
  width: 300px;
  height: 300px;
}
</style>



